.button {
  display: inline-block;
  padding: 8px 12px;
  border: 1px solid #fff;
  border-radius: 5px;
  font-weight: 300;
  line-height: 1;
  text-decoration: none;
  color: #fff;
  background-image:
    linear-gradient(
      45deg,
      rgba(73, 155, 234, 1) 0%,
      rgba(26, 188, 156, 1) 100%
    );
  filter:
    hue-rotate(
      0deg
    );
  transition: all 0.5s;

  &:hover {
    filter:
      hue-rotate(
        135deg
      );
  }

  &[disabled] {
    background-image: none;
  }

  &--red {
    padding: 7px 11px;
    border: 2px solid #a30606;
    color: #a30606;
    background-image: none;
    background-color: #fbb;
  }
}