.editable-span {
  &__span {
    position: relative;
    display: block;
    padding-left: 40px;
    font-size: 40px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 40px;
      height: 40px;
      background-image: url("/images/pen.svg");
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
}