@import "../../scss/var";

.header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  color: $green-100;
  background-color: $green-700;

  &__nav {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $green-500;
    transform: translateY(-500%);
    transition: transform 0.3s;
  }

  &__items {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__link {
    padding: 8px 12px;
    border: 1px solid $green-700;
    transition: background-color 0.5s;

    &:hover {
      background-color: $green-700;
    }

    &--active {
      background-color: $green-700;
    }
  }

  &:hover {
    .header__nav {
      transform: translateY(0);
    }
  }
}